import { Component, OnInit, Injector, Output, EventEmitter, Input } from '@angular/core';
import { Router, ActivatedRoute } from '@angular/router';
import { MatDialog } from '@angular/material/dialog';
import { CookieService } from 'ngx-cookie-service';
import * as moment from 'moment';
import { UserService } from 'src/app/api/controllers/User';
import { RentService } from 'src/app/api/controllers/Rent';
import { ApartmentService } from 'src/app/api/controllers/Apartment';
import { LookupService } from 'src/app/api/controllers/Lookup';
import { SharedService } from 'src/app/shared/services/shared.service';
import { ModalService } from 'src/app/shared/services/modal.service';

@Component({
    selector: 'app-create-lease-info',
    templateUrl: './create-lease-info.component.html',
    styleUrls: ['./create-lease-info.component.scss']
})
export class CreateLeaseInfoComponent implements OnInit {
    rent: any;
    isDataLoaded: boolean = true;
    isError: boolean = false;
    errorMessage: boolean = false;
    modalService: any;
    userId: any;
    @Input() apartmentBlockUnitId?:any;
    @Output() public outputParams = new EventEmitter();
    constructor(
        private router: Router, 
        private route: ActivatedRoute, 
        private injector: Injector, 
        public dialog: MatDialog, 
        private userService: UserService, 
        private rentService: RentService, 
        private apartmentService: ApartmentService, 
        private lookupService: LookupService, 
        private sharedService: SharedService, 
        private cookieService: CookieService) {
        this.modalService = this.injector.get(ModalService);
    }
    ngOnInit() {
        this.rent = {};
        this.userId = this.route.params['value'].id;
    }

    goBack() {
        this.outputParams.emit('back');
    }
          
    submitLeaseInfoForm(form){
        let details = {
          "apartmentBlockUnitId": this.apartmentBlockUnitId,
          "startDate": this.rent.startDate,
          "expiryDate": this.rent.expiryDate,
          "rentAmount": parseInt(this.rent.rentAmount),
          "advanceAmount": parseInt(this.rent.advanceAmount),
          "leaseAmount": parseInt(this.rent.leaseAmount),
          "rentTypeId": 0,
          "notes": "string",
          "field1": "string",
          "field2": "string",
          "isActive": true,
          "insertedBy": this.userId,
          "insertedOn": moment(new Date()).utc().format().toString(),
          "updatedBy": this.userId,
          "updatedOn": moment(new Date()).utc().format().toString()
        }
        let AddApartmentBlockUnitRentParams = {
            apartmentBlockUnitRent: details
        }
        this.rentService.addApartmentBlockUnitRent(AddApartmentBlockUnitRentParams).subscribe((res : any) => {
          if(res.message){
            this.sharedService.setAlertMessage("Lease Info added successfully");
            this.isDataLoaded = true;
            this.outputParams.emit('close')
        }
         },
      error => {
              console.log(error);
      });
      }
    
 
}
