import { Component, OnInit, ViewChild } from '@angular/core';
import { Router, ActivatedRoute } from '@angular/router';
import { SharedService } from 'src/app/shared/services/shared.service';
import { jqxGridComponent } from 'src/app/shared/jqwidgets-scripts/jqwidgets-ts/angular_jqxgrid';
import { CookieService } from 'ngx-cookie-service';
import * as moment from 'moment';
import { UserService } from 'src/app/api/controllers/User';
import { RentService } from 'src/app/api/controllers/Rent';
import { ApartmentService } from 'src/app/api/controllers/Apartment';
import { LookupService } from 'src/app/api/controllers/Lookup';

@Component({
  selector: 'app-profile-lease-info',
  templateUrl: './profile-lease-info.component.html',
  styleUrls: ['./profile-lease-info.component.scss']
})
export class ProfileLeaseInfoComponent implements OnInit {
  isAddInfo:boolean = false;
  isDataLoaded:boolean = false;
  rent: any;
  rentListData: any;
  isrentListData:boolean = false;
  apartmentBlockUnitId: any;
  userId: any;
  leaseData:string = "";
  totalItems:any;
  modalService:any;
  ticket:any;
  columnData:any;
  gridSourceData:any;
  @ViewChild('datagrid', { static: false }) datagrid: jqxGridComponent;

  constructor(
    private router: Router,
    private route: ActivatedRoute,
    private userService:UserService,
    private rentService:RentService,
    private apartmentService:ApartmentService,
    private lookupService: LookupService,
    private sharedService:SharedService,
    private cookieService:CookieService
  ) { }

  ngOnInit() {
    this.getListData();
  }

  getListData() {
    var cellsrenderer = (row, column, value) => {
      return '<div class="jqx-custom-inner-cell">' + value + '</div>';
    }

    var columnrenderer = (value) => {
      return '<div style="padding: 14px">' + value + '</div>';
    }


    this.columnData = [{
         text: 'Start Date',
         datafield: 'startDate',
         minwidth: 100,
         cellsrenderer: cellsrenderer,
         renderer: columnrenderer
     }, {
         text: 'Expiry Date',
         datafield: 'expiryDate',
         cellsrenderer: cellsrenderer,
         minwidth: 100,
         renderer: columnrenderer
     }];

    
    this.userId =  this.route.params['value'].id;

    this.apartmentService.getApartmentBlockUnitByUserId(this.userId).subscribe((res : any) => {
      let data = res[0] || {};
      this.apartmentBlockUnitId = data.apartmentBlockUnitId;
      this.rentService.getAllApartmentBlockUnitRents().subscribe((res : any) => {
      let sortData = res;
      sortData.sort((a, b) => new Date(b.startDate).getTime() - new Date(a.startDate).getTime());
      let rentListData = sortData.filter(data => {
        data.startDate = moment(data.startDate).format('MM/DD/YYYY');
        data.expiryDate = moment(data.expiryDate).format('MM/DD/YYYY');
        return data.isActive
      })
      this.gridSourceData = {
        localdata: rentListData,
        datatype: "array"
      }

      this.rentListData = new jqx.dataAdapter(this.gridSourceData);
      this.isDataLoaded = true;
     },
  error => {
          console.log(error);
  });
       
     },
  error => {
    console.log(error);
  });
    
  }
  onGlSearchFilter() {

    if(this.leaseData != ""){

      let filtergroup = new jqx.filter();
      let filter_or_operator = 1;
      let filtervalue = this.leaseData;
      let filtercondition = 'contains';
      let filterData = filtergroup.createfilter('stringfilter', filtervalue, filtercondition);
      filtergroup.operator = 'or';
      filtergroup.addfilter(filter_or_operator, filterData);

      this.datagrid.showfiltercolumnbackground(false);

      this.columnData.forEach(item => {
        if(item.datafield != 'Actions') {
          this.datagrid.addfilter(item.datafield, filtergroup, true);
        }
      })

      this.datagrid.applyfilters();
      
    }
    else {
      this.datagrid.clearfilters();
    }

  }

  showLeaseInfo(){
    this.isAddInfo = true;
  }

  
  getOutPutParams(params) {
    if(params === "back"){
      this.isAddInfo = false;
    }
    else {
     this.getListData();
    this.isAddInfo = false;
    }
}
 
}

function getClassName(value){
  var name = value.split('-');
  return name[0].toLowerCase();
}
(window as any).getClassName = getClassName;